.anim_cont{
    perspective: 1000px!important;
    transform-style: preserve-3d!important;
    width: -webkit-fit-content!important;
    width: -moz-fit-content!important;
    width: fit-content!important;
    padding: 0!important;
    margin: 0!important;
    box-shadow: none!important;
    border-radius: 0!important;
    background-color: transparent!important;
    line-height: normal!important;
    color: black!important;
}
.itemCard_cont{
    display: flex;
    justify-content: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-bottom: 30px;
    transform-style: preserve-3d!important;
    perspective: 1000px!important;
}
.itemCard_cont .item_card {
    background-color: hsl(0, 0%, 98%);
    height: 100%;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 20px;
    transform-origin: center;
    box-shadow: hsla(140, 77%, 38%,0.2) 0px 4px 12px;
}
.itemCard_cont .item_card .img_sec{
    height: 130px;
    width: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #7ee8fa;
    background-image: linear-gradient(315deg, #7ee8fa 0%, #80ff72 74%);

    border-radius: 100%;
}
.itemCard_cont .item_card .img_sec .image{
    height: 100px;
    transition: 0.5s ease-in;
}
.itemCard_cont .item_card .title{
    font-size: 20px;
    font-weight: 500;
    margin-top: 30px;
    transition: 0.5s ease-in;
}
.itemCard_cont .item_card .desc{
    margin-top: 10px;
    font-size: 14px;
    text-align: center;
    transition: 0.5s ease-in;
}
.itemCard_cont .item_card .view_btn{
    margin-top: 20px;
    width: 90%;
    text-align: center;
    padding: 5px;
    font-size: 16px;
    border-radius: 40px;
    border: 2px solid hsl(140, 77%, 38%);
    color: hsl(140, 77%, 38%);
    font-weight: 500;
    transition: 0.5s ease-in-out;
    cursor: pointer;
    transition: 0.5s ease-in;
}
.itemCard_cont .item_card .view_btn:hover{
    background-color: hsl(140, 77%, 38%);
    color: white;
}
.itemCard_cont .item_card .view_btn i{
    font-size: 18px;
    font-weight: 500;
    margin-left: 5px;
    animation: btn_arrow 2s ease-in-out infinite;
}
@keyframes btn_arrow {
    0%{
        transform: translateX(8px);
    }
    50%{
        transform: translateX(0%);
    }
    100%{
        transform: translateX(8px);
    }
}
/* .itemCard_cont .item_card:hover{
    transition: none;
} */
.itemCard_cont .item_card:hover .image{
    transform: translateZ(-50px);
}
.itemCard_cont .item_card:hover .title{
    transform: translateZ(40px);
}
.itemCard_cont .item_card:hover .desc{
    transform: translateZ(30px);
}
.itemCard_cont .item_card:hover .view_btn{
    transform: translateZ(20px);
}
.top_pick{
    margin-top: 50px;
}
.top_pick >.heading{
    font-size: 28px;
    position: relative;
    padding-bottom: 10px;
    margin-bottom: 50px;
    overflow: hidden;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.top_pick >.heading:after{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    height: 4px;
    width: 70%;
    background-color: hsl(140, 77%, 38%);
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    animation: heading 10s ease-in-out infinite;
}
.owl-carousel{
    position: relative;
}
.owl-carousel .owl-item img{
    width: -webkit-max-content!important;
    width: -moz-max-content!important;
    width: max-content!important;
}
.owl-carousel .owl-item{
    display: flex;
    justify-content: center;
}
.owl-theme .owl-dots .owl-dot.active span,
.owl-theme .owl-dots .owl-dot:hover span{
    background-color: hsl(140, 77%, 38%)!important;
    transition: 0.25s ease-in-out;
}
.owl-carousel .owl-nav button.owl-prev,
.owl-carousel .owl-nav button.owl-next{
    position: absolute;
    top: 40%;
    height: 40px;
    width: 40px;
    background-color: hsl(140, 77%, 38%)!important;
    font-size: 18px;
    transform: translateY(-40%);
    border-radius: 100%!important;
    background-position: center!important;
    background-repeat: no-repeat!important;
    background-size: contain!important;
    color: white;
    transition: 0.5s ease-in-out;
}
.owl-carousel .owl-nav button.owl-prev{
    left: 0;
    background-image: url(/GSASports/static/media/leftarrow.4e33043a.svg)!important;
}
.owl-carousel .owl-nav button.owl-next{
    right: 0;
    background-image: url(/GSASports/static/media/rightarrow.02127f18.svg)!important;
}
.owl-carousel .owl-nav button.owl-prev:hover,
.owl-carousel .owl-nav button.owl-next:hover{
    background-color: hsl(140, 77%, 47%)!important;
}
.owl-carousel .owl-nav button.owl-prev span,
.owl-carousel .owl-nav button.owl-next span{
    display: none;
}

.circle_Loader{
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.circle_Loader .circ{
    padding: 5px;
    border-radius: 100%;
    border: 2px solid transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}
.circle_Loader .circ1{
    height: 100px;
    width: 100px;
    border-top: 3px solid hsl(347, 100%, 50%);
    animation: rotate 2s ease infinite;
}
.circle_Loader .circ2{
    height: 80px;
    width: 80px;
    border-top: 3px solid hsl(140, 77%, 38%);
    animation: rotate 4s ease infinite;
}
.circle_Loader .circ3{
    height: 60px;
    width: 60px;
    border-top: 3px solid rgb(0, 65, 185);
    animation: rotate 6s ease infinite;
}
@keyframes rotate {
    from{
        transform: rotateZ(0deg);
    }
    to{
        transform: rotateZ(360deg);
    }
}

.homepage{
    padding-top: 8.5vh;
}
.homepage .video_sec{
    position: relative;
    height: 91.5vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.homepage .video_sec video{
    position: absolute; 
    top: -85px;
    right: 0px;
    min-width: 100%;
    min-height: 100%;
    z-index: -1;
}
.homepage .video_sec .overlay{
    position: absolute;
    top: 15px;
    bottom: 15px;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 3;
    color: white;
}
.homepage .video_sec .overlay div{
    font-size: 50px;
    font-family: 'Dancing Script', cursive;
    padding: 30px 50px;
}
.homepage .video_sec::after{
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 65%;
    height: 15px;
    background-color: hsl(140, 77%, 38%, 0.9);
    color: white;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 1% 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 1% 100%);
}
.homepage .video_sec::before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 65%;
    height: 15px;
    background-color: hsl(140, 77%, 38%, 0.9);
    color: white;
    -webkit-clip-path: polygon(0 0, 99% 0, 100% 100%, 0 100%);
            clip-path: polygon(0 0, 99% 0, 100% 100%, 0 100%);
}
.homepage .top_pick_cont{
    padding: 30px 5vw 50px!important;
}
.homepage .section1 .heading{
    position: relative;
    font-size: 28px;
    margin-left: 5vw;
    padding-bottom: 10px;
    margin-bottom: 40px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
