.contact_cont .email.err .form input:focus + .label-name .content-name,
.contact_cont .email.err .form input:valid + .label-name .content-name,
.contact_cont .phone.err .form input:focus + .label-name .content-name,
.contact_cont .phone.err .form input:valid + .label-name .content-name{
    color: red;
}

.contact_cont .form input:focus + .label-name::after, 
.contact_cont .form input:valid + .label-name::after{
    transform: translateX(0%);
}

.contact_cont .form_cont .notes .heading{
    font-size: 20px;
    margin-top: 10px;
    font-weight: 500;
}

.contact_cont .form_cont textarea{
    width: 95%;
    height: 125px;
    padding: 5px 10px;
    font-size: 18px;
    border-radius: 5px;
    outline: none;
    border: 1px solid black;
    transition: 0.5s ease-in-out;
}
.contact_cont .form_cont .notes.err textarea{
    border: 2px solid red;
}
.contact_cont .form_cont textarea:hover{
    border: 1px solid hsl(140, 77%, 38%);
}
.contact_cont .submit{
    grid-column: 1 / span 2;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    justify-self: center;
    margin-top: 20px;
    padding: 10px 25px;
    background-color: hsl(140, 77%, 38%);
    color: white;
    border-radius: 50px; 
    font-size: 18px;
    transition: 0.5s ease-in-out;
    border: 0px;
    cursor: pointer;
}
.contact_cont .submit:hover{
    text-decoration: none;
    background-color: hsl(140, 87%, 48%);
    transform: translateY(-10px);
    box-shadow: 0 5px 10px rgba(78, 78, 78, 0.4);
}
.contact_cont .section2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-left: 2px solid rgb(0, 185, 62);
    padding-left: 40px;
    padding-bottom: 40px;
}

.contact_cont .section2 .heading .big{
    font-size: 32px;
    font-weight: 600;
}
.contact_cont .section2 .contact_us .reach{
    font-size: 17px;
    margin-top: 25px;
    cursor: pointer;
    display: flex;
}
.contact_cont .section2 .contact_us .reach a{
    color: black;
}
.contact_cont .section2 .contact_us .reach.user{
    font-size: 20px;
    font-weight: 500;
}

.contact_cont .section2 .contact_us i{
    width: 40px;
    font-size: 27px;
}
.contact_cont .social{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.contact_cont .socialcontainer{
    padding: 50px 30px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.contact_cont .social-icons a {
    display: inline-block;
    width: 45px;
    height: 45px;
    font-size: 24px;
    text-align: center;
    text-decoration: none;
    border-radius: 50%;
    margin-right: 15px;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
}
.contact_cont .social-icons a .fa{
    line-height: 45px;
}
.contact_cont .social-icons .mob{
    color: black;
    animation-name: mob;
    animation-direction: normal;
    animation-duration: 10s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-play-state: running;
    animation-timing-function: linear;
}
@keyframes mob{
    0%{
        color: black;
        background-color: transparent;
        transform: translateY(0px) scale(1);
    }
    10%{
        background: black;
        color: white;
        transform: translateY(-10px) scale(1.1);
    }
    20%{
        color: black;
        background-color: transparent;
        transform: translateY(0px) scale(1);
    }
}
.contact_cont .social-icons .mob:hover{
    background: black;
    color: white;
    transform: translateY(-10px) scale(1.1);
}
.contact_cont .social-icons .loc{
    color: #4267B2;
    animation-name: loc;
    animation-direction: normal;
    animation-duration: 10s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-play-state: running;
    animation-timing-function: linear;
}
@keyframes loc{
    20%{
        color: #4267B2;
        background-color: transparent;
        transform: translateY(0px) scale(1);
    }
    30%{
        background-color: #4267B2;
        color: white;
        transform: translateY(-10px) scale(1.1);
    }
    40%{
        color: #4267B2;
        background-color: transparent;
        transform: translateY(0px) scale(1);
    }
}
.contact_cont .social-icons .loc:hover{
    background: #4267B2;
    color: white;
    transform: translateY(-10px) scale(1.1);
}
.contact_cont .social-icons .mail{
    color: #c71610;
    animation-name: mail;
    animation-direction: normal;
    animation-duration: 10s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-play-state: running;
    animation-timing-function: linear;
}
@keyframes mail{
    40%{
        color: #c71610;
        background-color: transparent;
        transform: translateY(0px) scale(1);
    }
    50%{
        background-color: #c71610;
        transform: translateY(-10px) scale(1.1);
        color: white;
    }
    60%{
        color: #c71610;
        background-color: transparent;
        transform: translateY(0px) scale(1);
    }
}
.contact_cont .social-icons .mail:hover {
    background: #c71610;
    color: white;
    transform: translateY(-10px) scale(1.1);
}
.contact_cont .social-icons .yt{
    color: #FF0000;
    animation-name: yt;
    animation-direction: normal;
    animation-duration: 10s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-play-state: running;
    animation-timing-function: linear;
}
@keyframes yt{
    60%{
        color: 	#FF0000;
        background-color: transparent;
        transform: translateY(0px) scale(1);
    }
    70%{
        background-color: #FF0000;
        transform: translateY(-10px) scale(1.1);
        color: white;
    }
    80%{
        color: #FF0000;
        background-color: transparent;
        transform: translateY(0px) scale(1);
    }
}
.contact_cont .social-icons .yt:hover {
    background: #FF0000;
    color: white;
    transform: translateY(-10px) scale(1.1);
}
.contact_cont .social-icons .link{
    color: #E1306C;
    animation-name: link;
    animation-direction: normal;
    animation-duration: 10s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-play-state: running;
    animation-timing-function: linear;
}
@keyframes link{
    80%{
        transform: translateY(0px) scale(1);
        color: #E1306C;
        background-color: transparent;
    }
    90%{
        transform: translateY(-10px) scale(1.1);
        background-color: #E1306C;
        color: white;
    }
    100%{
        color: #E1306C;
        background-color: transparent;
        transform: translateY(0px) scale(1);
    }
}
.contact_cont .social-icons .link:hover {
    background-color: #E1306C;
    color: white;
    transform: translateY(-10px) scale(1.1);
}
.contact_cont .socialcontainer:hover .social-icons .mob,
.contact_cont .socialcontainer:hover .social-icons .loc,
.contact_cont .socialcontainer:hover .social-icons .mail,
.contact_cont .socialcontainer:hover .social-icons .yt,
.contact_cont .socialcontainer:hover .social-icons .link{
    animation-name: none;
}

@media only screen and (max-width:1075px){

    .contact_cont .section1 .heading .big{
        font-size: 28px;
    }
    .contact_cont .section2 .heading .big{
        font-size: 24px;
