    .contact_cont .form_cont{
        padding-top: 0px;
        display: flex;
        flex-direction: column;
        align-items: stretch;
    }
    .contact_cont .form_cont .option{
        width: 90%;
        margin-top: 20px;
        margin-bottom: 10px;
        font-size: 18px;
    }
    .form{
        font-size: 18px;
    }
    .contact_cont .form input:focus + .label-name .content-name,
