        font-size: 16px;
    }
    .contact_cont .form_cont .notes .heading{
        font-size: 18px;
    }
    .contact_cont .form_cont .option{
        display: none;
    }
    .contact_cont .form_cont .submit{
        align-self: center;
    }
    .contact_cont .form_cont textarea{
        font-size: 16px;
    }
    .contact_cont .submit_cont .submit{
        font-size: 18px;
    }
    .contact_cont .section1 .heading .big{
        font-size: 24px;
    }
    .contact_cont .section2 .heading .big{
        font-size: 18px;
        margin-top: 40px;
    }
    .contact_cont .section2 .contact_us .reach{
        font-size: 16px;
