    flex-direction: column;
    justify-content: center;
    height: 100%;
}
.prod_det .content .desc{
    margin-bottom: 40px;
    text-align: justify;
    font-size: 16px;
}
.prod_det .content .size{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
}
.prod_det .content .size .heading{
    font-size: 18px;
    font-weight: 500;
    margin-right: 20px;
}
.prod_det .content .size .s{
    margin: 0 10px;
    border-radius: 100%;
    height: 40px;
    width: 40px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: lightgreen;
    transition: 0.5s ease-in-out;
}
.prod_det .content .size .s:hover{
    transform: scale(1.1);
}
.prod_det .content .size .s.active{
    font-weight: 500;
    background-color: hsl(140, 76%, 50%);
    border: 2px solid hsl(140, 100%, 34%);
    color: white;
    transform: scale(1.1);
}
.prod_det .content .quantity{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
}
.prod_det .content .quantity .counter{
    display: flex;
}
.prod_det .content .quantity .counter input{
    border: 1px solid lightgreen;
    border-right: none;
    outline: none;
    padding: 5px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    width: 50px;
    text-align: center;
}
.prod_det .content .quantity .counter .controls .up{
    padding: 3px 15px;
    font-size: 13px;
    border: 1px solid lightgreen;
    border-top-right-radius: 5px;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}
.prod_det .content .quantity .counter .controls .down{
    padding: 3px 15px;
    font-size: 12px;
    border: 1px solid lightgreen;
    border-bottom-right-radius: 5px;
    cursor: pointer;
    border-top: none;
    transition: 0.5s ease-in-out;
}
.prod_det .content .quantity .counter .controls .up:hover,
.prod_det .content .quantity .counter .controls .down:hover{
    background-color: lightgreen;
}
.prod_det .content .cart_cont{
    display: flex;
    justify-content: center;
    align-items: center;
}
.prod_det .content .cart_cont .cart_btn{
    font-size: 18px;
    font-weight: 500;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: 50px;
    color: white;
    padding: 7px 40px;
    background-color: hsl(140, 100%, 34%);
    border: 2px solid hsl(140, 76%, 50%);
    cursor: pointer;
    transition: 0.5s ease-in-out;
}
.prod_det .content .cart_cont .cart_btn:hover{
    transform: scale(1.03);
    box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px;
}
.prod_det .content .cart_cont .cart_btn img{
    height: 20px;
    margin-left: 5px;
    animation: btn_arrow 4s ease-in-out infinite;
}
@keyframes btn_arrow {
    0%{
        transform: translateX(8px);
    }
    50%{
        transform: translateX(0%);
    }
    100%{
        transform: translateX(8px);
    }
}
.prod_det .content .cart_cont span{
    font-weight: 500;
    margin-left: 20px;
    opacity: 0;
    transform: translateY(20px);
    transition: 0.5s ease-in-out;
}
.prod_det .content .cart_cont span.show{
    color: hsl(140, 100%, 34%);
    opacity: 1;
    transform: translateY(0px);
}
.prod_det .content .cart_cont span.error{
    color: red;
    opacity: 1;
    transform: translateY(0px);
}
