.cart{
    padding-top: 100px;
}
.cart .head{
    position: relative;
    font-size: 28px;
    padding-bottom: 10px;
    margin-bottom: 40px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    overflow: hidden;
}
.cart .head::after{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    height: 4px;
    width: 150px;
    background-color: hsl(140, 77%, 38%);
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    animation: heading 10s ease-in-out infinite;
}
.cart > .heading{
    padding: 15px 0;
    text-align: center;
    border-bottom: 2px solid lightgreen;
    font-size: 18px;
}
.cart .cart_item{
    border-bottom: 2px solid lightgreen;
    padding: 10px 0;
    position: relative;
}
.cart .cart_item .img_cont{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.cart .cart_item .img_cont img{
    height: 80px;
}
.cart .cart_item .img_cont .detail{
    font-size: 16px;
    text-align: center;
}
.cart .cart_item .img_cont .detail div{
    font-size: 14px;
}
.cart .cart_item .price,
.cart .cart_item .total{
    display: flex;
    justify-content: center;
    align-items: center;
}
.cart .cart_item .quantity{
    display: flex;
    justify-content: center;
    align-items: center;
}
.cart .cart_item .quantity .counter{
    display: flex;
}
.cart .cart_item .quantity .counter input{
    border: 1px solid lightgreen;
    border-right: none;
    outline: none;
    padding: 5px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    width: 50px;
    text-align: center;
}
.cart .cart_item .quantity .counter .controls .up{
    padding: 3px 15px;
    font-size: 13px;
    border: 1px solid lightgreen;
    border-top-right-radius: 5px;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}
.cart .cart_item .quantity .counter .controls .down{
    padding: 3px 15px;
    font-size: 12px;
    border: 1px solid lightgreen;
    border-bottom-right-radius: 5px;
    cursor: pointer;
    border-top: none;
    transition: 0.5s ease-in-out;
}
.cart .cart_item .quantity .counter .controls .up:hover,
.cart .cart_item .quantity .counter .controls .down:hover{
    background-color: lightgreen;
