.cart .cart_item .cart_btn_cont{
    display: flex;
    justify-content: center;
    align-items: center;
}
.cart .cart_item .cart_btn_cont .cart_btn{
    padding: 10px 30px;
    margin-right: 40px;
    border-radius: 50px;
    border: 2px solid #cd6bda;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}
.cart .cart_item .cart_btn_cont .cart_btn:hover{
    background-color: #cd6bda;
