.cart .checkout_cont .checkout_btn{
    background-color: hsl(140, 77%, 45%);
    color: white;
    border-radius: 50px;
    margin-top: 50px;
    padding: 10px 50px;
    font-size: 20px;
    cursor: pointer;
    border: 2px solid hsl(140, 77%, 45%);
