.adminLogin{
    padding: 100px 5vw;
    min-height: 100vh;
    background-image: url(/GSASports/static/media/adminLogin.178a9e35.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
}
.adminLogin .login{
    position: relative;
    z-index: 1;
    padding: 100px 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 400px;
}
.adminLogin .login .username,
.adminLogin .login .password{
    width: 100%;
    margin-bottom: 20px;
}
.adminLogin .login:before{
    z-index: -1;
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(/GSASports/static/media/adminLogin.178a9e35.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    -webkit-filter: blur(2px);
            filter: blur(2px);
    box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 15px;
}

.adminLogin .login .form{
    width: 100%;
    position: relative;
    height: 70px;
    overflow: hidden;
    color: white;
}
.adminLogin .login .form input{
    width: 100%;
    height: 100%;
    font-size: 16px;
    color: white;
    padding-top: 30px;
    padding-left: 10px;
    border: none;
    outline: none;
    background: rgba(255, 255, 255, 0);
}
.adminLogin .login .form label{
    font-size: 18px;
    position: absolute;
    bottom: 0px;
    left: 0%;
    width: 100%;
    height: 100%;
    pointer-events: none;
    border-bottom: 1px solid white;
}
.adminLogin .login .form label::after {
    content: "";
    position: absolute;
    left: 0px;
    bottom: -1px;
    width: 100%;
    height: 100%;
    border-bottom: 4px solid hsl(140, 87%, 48%);
    transform: translateX(-100%);
    transition: transform 0.3s ease;
}

.adminLogin .login .content-name{
    position: absolute;
    bottom: 0px;
    left: 0px;
    transition: all 0.3s ease;
    font-weight: 500;
}

.adminLogin .login .form input:focus + .label-name .content-name,
.adminLogin .login .form input:valid + .label-name .content-name {
    transform: translateY(-120%);
    font-size: 19px;
    color: hsl(140, 87%, 48%);

}

.adminLogin .login .form input:focus + .label-name::after, 
.adminLogin .login .form input:valid + .label-name::after{
    transform: translateX(0%);
}

.adminLogin .login .submit{
    grid-column: 1 / span 2;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    justify-self: center;
    margin-top: 20px;
    padding: 10px 25px;
    background-color: hsl(140, 77%, 38%);
    color: white;
    border-radius: 50px; 
    font-size: 18px;
    transition: 0.5s ease-in-out;
    border: 0px;
    cursor: pointer;
}
.adminLogin .login .submit:hover{
    text-decoration: none;
    background-color: hsl(140, 87%, 48%);
    transform: translateY(-10px);
    box-shadow: 0 5px 10px rgba(78, 78, 78, 0.4);
}

.adminLogin .login .msg{
    margin-top: 30px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: 0.5s ease-in-out;
    text-decoration: underline;
    color: black;
}
.adminLogin .login .msg:hover{
    color: hsl(140, 100%, 50%);
}
@media only screen and (max-width:550px){

    .form{
        font-size: 18px;
    }
    .adminLogin .login .form input:focus + .label-name .content-name,
    .adminLogin .login .form input:valid + .label-name .content-name {
        font-size: 16px;
    }
    .adminLogin .login .submit_cont .submit{
        font-size: 18px;
    }
}
